<template>
<v-app>
  <v-toolbar fixed flat class="mainToolbar">
    <v-container class="homeContainer">
      <v-layout class="rowz text-xs-center">
        <v-flex xs12>
          <div class="titl">
            <router-link class="titleMain" to="/">
              <h1 @click="moveTop">Milan Petrovic</h1>
            </router-link>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </v-toolbar>
  <v-container class="homeContainer margtop"  fluid grid-list-sm>
    <v-layout row wrap class="text-xs-center pictrans">
      <v-flex xs12 md6 v-for="pic in pics" :key="pic.id" @click="moveTop">
        <router-link :to="'/album/'+ pic.id">
          <clazy-load :src="getPic(pic.thumbnail)" class="thumb">
            <img :src="getPic(pic.thumbnail)" slot="image">
            <v-flex slot="placeholder">
              <div></div>
            </v-flex>
          </clazy-load>
        </router-link>
        <v-flex v-text="pic.text" ></v-flex>
      </v-flex>
    </v-layout>
  </v-container>
</v-app>
</template>
<script>
export default {
  data() {
    return {

    }
  },
  methods: {
    getPic(pic) {
      return require('@/assets/img/' + pic)
    },
    moveTop() {
      window.scrollTo(0, 0)
    }
  },
  computed: {
    pics() {
      return this.$store.getters.loadAlbums
    }
  }
}
</script>
<style>

</style>



