<template>
<v-app id="app" v-cloak>
  <div>
    <transition name="fade">
      <router-view/>
    </transition>
  </div>
  <homeView></homeView>
  <foot></foot>
</v-app>
</template>
<script>
import homeView from './components/homeComponent'
import foot from './components/footer'



export default {
  name: 'app',
  components: {
    homeView,
    foot
  }
}
</script>

<style media="screen" >
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono');

@font-face {
  font-family: 'Roboto Mono', monospace;
  src: url('./assets/fonts/RobotoMono-Regular.ttf') format('ttf');
  font-weight: normal;
  font-style: normal;
}

#app {
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}
h1{
  font-size: 30px;
}

.application.theme--light {
  background: #fcfcfc !important;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  padding: 0 !important;
}

.mainContainer {
  max-width: 1000px !important;
  padding: 0 !important;
}

.homeContainer {
  max-width: 1050px !important;
  padding: 0;
}

.mainToolbar {
  height: 100px;
  background-color: #fcfcfc !important;
  line-height: 100px!important;
}

.thumb{
  padding: 8px 20px;
}
.titleMain
 {
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.margtop {
  margin-top: 100px !important;
}

.fade-enter-active {
  transition: .3s ease-in-out;
}

.fade-leave-active,
.fade-enter {
  transform: translatex(-3%);
  filter: blur(20px);
}

.socialMedia {
  height: 40px;
  width: 40px;
}
.pictrans {
animation: 1.4s ease-out fadein;
transition: 1.4s;
}
@keyframes fadein {
0% {
  opacity: 0;
  transform: translate(100%);
}
70% {
  opacity: 0;
  transform: translate(-4%,10%);
  filter: blur(30px);
}
100% {
  opacity: 1;
  transform: translateY(0);
  filter: blur(0);
}
}
[v-cloak] {
  display: none
}
</style>



